/* ---------------------------------- */
/* Forms Reset & Styles
------------------------------------- */

input,
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"],
textarea,
select {
	height: 51px;
	line-height: 51px;
	padding: 0 20px;
	outline: none;
	font-size: 15px;
	color: #808080;
	margin: 0 0 16px 0;
	max-width: 100%;
	width: 100%;
	box-sizing: border-box;
	display: block;
	background-color: #fff;
	border: 1px solid #dbdbdb;
	box-shadow: 0 1px 3px 0px rgba(0, 0, 0, 0.06);
	font-weight: 500;
	opacity: 1;
	border-radius: 3px;
}

input:-webkit-autofill:focus,
input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #fff inset !important;
    -webkit-text-fill-color: #808080 !important;
}

select {
	padding: 15px 18px;
	cursor: pointer;
}

input {
	-webkit-transition: all 0.1s ease-in-out;
	-moz-transition: all 0.1s ease-in-out;
	-o-transition: all 0.1s ease-in-out;
	-ms-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

input:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="number"]:focus,
textarea:focus {
	color: #808080;
	transition: box-shadow 0.2s !important;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.07);
	border: 1px solid #d8d8d8;
	opacity: 1;
}

input[type="submit"] {
  border: none;
  padding: 11px 18px;
  width: auto;
}

input[type="checkbox"] { display: inline; }

input[type="radio"] {
	width: 15px;
	height: 15px;
	cursor: pointer;
	box-shadow: none;
}

/* Input Placeholder Color */
::-webkit-input-placeholder { /* WebKit browsers */
	color: #888;
	opacity: 1;
}

:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
	color: #888;
	opacity: 1;
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
	color: #888;
	opacity: 1;
}

:-ms-input-placeholder { /* Internet Explorer 10+ */
	color: #888;
	opacity: 1;
}

textarea {
	height: auto;
	line-height: 27px;
	padding: 20px;
	min-height: 130px;
	transition: none !important;
	min-width: 100%;
}

label,
legend {
	display: block;
	font-weight: bold;
	font-size: 15px;
	font-weight: normal;
	margin-bottom: 8px;
}


label span,
legend span {
	font-weight: normal;
	font-size: 14px;
	color: #444;
}

fieldset {
    padding: 0;
    border: none;
}


a, button { outline: none !important; }
a:focus,
a:hover { text-decoration: none; color: #333;}
img { max-width: 100%; }


/* ---------------------------------- */
/* Header
------------------------------------- */
#logo { display: inline-block; margin-top: 0px; }

#logo img { max-height: 50px; width: auto; transform: translate3d(0,0,0); }
img.footer-logo { max-height: 50px; width: auto; }

#header {
	/* padding-top:5px; */
	position: relative;
	z-index: 999;
	padding: 18px 0 8px 0;
	box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.12);
	font-size: 16px;
}

/* Header Widget */
.header-widget {
	position: relative;
	top: -1px;
	height: 54px;
}

.header-widget .button,
.header-widget .button.border {
	margin-right: 0;
	min-width: 130px;
	text-align: center;
	position: relative;
	line-height: 26px;
	font-weight: 500;
}

.header-widget .button.border i {
	font-size: 17px;
	padding: 0px 0 0 4px;
	top: 2px;
	display: inline-block;
	position: relative;
}

.header-widget .sign-in {
	position: relative;
	color: #333;
	margin-right: 25px;
	top: 10px;
	display: inline-block;
	vertical-align: top;
}

.header-widget .sign-in i { padding-right: 4px; font-size: 15px; }
.header-widget .sign-in:hover { color: #66676b; }


/* Full Width Header */
header.fullwidth .container {
	width: 100%;
	margin: 0;
	padding: 0 40px;
}

.left-side {
	float: left;
	width: 65%;
	display: inline-block;

}

.right-side {
	float: right;
	width: 35%;
	display: inline-block;
}

/* mobile fix */
@media (min-width: 1025px) and (max-width: 1239px)  {
	.right-side {
		width: auto;
		display: inline-block;
		margin-top: -126px;
		position: relative;
	}

	.left-side {
		width: 100%;
		display: inline-block;
	}
}

#logo,
#navigation { float: left; }

#logo { margin-right: 50px; }

.header-widget { text-align: right; }


/* ---------------------------------- */
/* Sticky Header
------------------------------------- */
#header.cloned {
	position: fixed;
	top: 0px;
	width: 100%;
	z-index: 999;
}

#header.cloned { background-color: #fff; }

/* Sticky header styles */
#header.cloned #logo img { transform: translate3d(0,0,0); max-width: 120px; margin-top: 1px; }
#header.cloned { padding: 18px 0 7px 0; }

.sticky-logo { display: none; }

#header.cloned.unsticky {
	opacity: 1;
	visibility: hidden;
    pointer-events: none;
	transform: translate(0,-100%) scale(1);
	transition: 0.3s;
}

#header.cloned.sticky {
	opacity: 1;
	visibility: visible;
	transform: translate(0,0) scale(1);
	transition: 0.3s;
}


/*----------------------------------- */
/* Footer
------------------------------------- */
#footer {
	padding: 70px 0 0 0;
	background-color: #fff;
	border-top: 1px solid rgba(0,0,0,0.09);
	font-size: 16px;
}

#footer.sticky-footer { box-shadow: none; }
#footer.dark { border-top: none; }

#footer h4 {
	color: #333;
	font-size: 21px;
	margin-bottom: 25px;
}

.footer-shadow {
	width: 100%;
	display: block;
	background: #fff;
	height: 200px;
	margin-top: -200px;
	position: relative;
	z-index: -1;
	box-shadow: 0 20px 40px -20px rgba(0,0,0,0.06);
}

.text-widget span { color: #707070; }
.text-widget span.address { display: block; margin-bottom: 10px;}

#footer a {
	color: #66676b;
}

/*  Links  */
#footer ul.footer-links {
	margin: 0 0 15px 0;
	padding: 0;
	width: 50%;
	box-sizing: border-box;
	display: inline-block;
	float: left;
}

#footer .footer-links li { position: relative; list-style: none; }

#footer .footer-links li a {
	padding: 0 0 9px 16px;
	display: inline-block;
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	-ms-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
	color: #707070;
	line-height: 21px;
}

#footer .footer-links li a:hover {
	padding-left:22px;
	color: #66676b;
}

#footer .footer-links li:before {
	font-family: "FontAwesome";
	font-size: 20px;
	content: "\f105";
	position: absolute;
	left: 0;
	top: -2px;
	padding: 0 7px 0 0;
	color: #ccc;
}


/* Footer Bottom */
.copyrights {
	color: #707070;
	border-top: 1px solid rgba(0,0,0,0.09);
	margin-top: 60px;
	text-align: center;
	padding: 40px 0;
	display: inline-block;
	width: 100%;
}

#footer.alt .copyrights {
	margin-top:0;
	border: none;
	padding: 25px;
}

.copyrights a { color: #707070; }


/* Dark Styles */
#footer.dark {
	background: #222;
	color: #aaa;
}

#footer.dark a,
#footer.dark h4,
#footer.dark a:hover {
	color: #fff;
}

#footer.dark .text-widget,
#footer.dark .text-widget span,
#footer.dark .footer-links li a {
	color: #aaa;
}

#footer.dark .footer-links li a:hover { color: #fff; }
#footer.dark .footer-links li:before { color: #666; }

#footer.dark .copyrights {
	color: #aaa;
	border-top: 1px solid rgba(255,255,255,0.09);
}

#footer .social-icons li a {
    opacity: 0.8;
    transition: opacity 0.35s;
}

#footer .social-icons li a:hover { opacity: 1; }


/* ---------------------------------- */
/* Back to top
------------------------------------- */
#backtotop {
	position: fixed;
	right: 0;
	opacity: 0;
	visibility: hidden;
	bottom: 25px;
	margin: 0 25px 0 0;
	z-index: 999;
	transition: 0.35s;
	transform: scale(0.7);
}

#backtotop.visible {
	opacity: 1;
	visibility: visible;
	transform: scale(1);
}

#backtotop a {
	text-decoration: none;
	border: 0 none;
	display: block;
	width: 46px;
	height: 46px;
	background-color: #66676b;
	opacity: 1;
    transition: all 0.3s;
    border-radius: 50%;
    text-align: center;
    font-size: 26px
}

body #backtotop a { color: #fff; }

#backtotop a:after {
	content: "\f106";
	font-family: "FontAwesome";
	position: relative;
	display: block;
	top: 50%;
	-webkit-transform: translateY(-55%);
	transform: translateY(-55%);
}


/* ---------------------------------- */
/* Navigation
------------------------------------- */

#navigation {
	position: relative;
	display: block;
}

#navigation ul {
	list-style: none;
	position: relative;
	float: left;
	margin: 0;
	padding: 0;
}

#navigation ul a {
	display: block;
	text-decoration: none;
}

#navigation ul li {
	position: relative;
	float: left;
	margin: 0;
	padding: 0
}

#navigation ul li ul {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: 100%;
	left: 0;
	padding: 0;
	z-index: 99999;
}

#navigation ul ul li { float: none; }

#navigation ul ul ul {
	top: 0;
	left: 100%;
	margin: 0;
}

#navigation ul ul {
	margin: 10px 0 0 15px;
}

#navigation ul ul:before {
	content: "";
	position: absolute;
	left: 0;
	top: -10px;
	background: rgba(255,255,255,0);
	width: 100%;
	height: 10px;
}

#navigation ul ul ul:after {
	content: "";
	position: absolute;
	width: 10px;
	height: 100%;
	left: -10px;
	top: 0;
	opacity: 0;
}

#navigation ul li:hover > ul {
	opacity: 1;
	visibility: visible;
}

/* Indicator Arrow */
#navigation ul li a:after,
#navigation ul ul li a:after {
	font-family: "FontAwesome";
	opacity: 0.5;
}

#navigation ul li a:after { content: '\f107'; padding-left: 7px; }
#navigation ul ul li a:after {
	content: '\f105';
	position: absolute;
	right: 15px;
	top: 7px;
}

#navigation ul li a:only-child:after { content: ''; padding: 0; }
#navigation ul ul li a:only-child:after { content: '';}


/* Dropdown Styles
------------------------------------- */
#navigation ul ul {
	background-color: #fff;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.12);
	border-radius: 4px;
	font-size: 15px;
}

#navigation ul ul li { border-radius: 4px; }
#navigation ul ul li:last-child { border-bottom: none; }

#navigation ul li:hover ul a,
#navigation ul ul a {
	line-height: 27px !important;
	padding: 4px 40px 4px 15px !important;
	line-height: 24px !important;
}

body #navigation ul ul {
	padding: 12px 10px;
	box-sizing: border-box;
}

#navigation ul ul li { width: 180px; }

#navigation ul ul li:hover {
	border-radius: 0;
}

#navigation ul ul li:first-child { border-radius: 4px 4px 0 0; }
#navigation ul ul li:last-child { border-radius: 0 0 4px 4px; }

#navigation ul ul {
	opacity: 0;
	visibility: hidden;
	transition: all 0.25s;
	transform: translate3d(0,15px,0);
}

#navigation ul li:hover > ul {
	opacity: 1;
	visibility: visible;
	transform: translate3d(0,0,0);
}

/* Menu
------------------------------------- */
#navigation.style-1 .current {
	background-color: transparent;
	color: #66676b;
}

#navigation.style-1 { margin-top: 1px; }

#navigation.style-1 .current:after { opacity: 1; }

#navigation.style-1 ul li {
	transition: all 0.25s;
}

#navigation.style-1 ul li a { color: #444; cursor: pointer; }
body .user-menu ul li a:hover,
#navigation.style-1 ul li:hover a { color: #66676b; }

#navigation.style-1 ul a {
	line-height: 32px;
	padding: 5px 15px;
}

#navigation.style-1 ul li { margin: 0 5px; }

#navigation.style-1 ul ul li { margin: 0; border-radius: 0; }

#navigation.style-1 ul ul li:first-child { border-radius: 4px 4px 0 0; }
#navigation.style-1 ul ul li:last-child { border-radius: 0 0 4px 4px; }

/* Indicator Arrow */
#navigation ul ul li a:after {
	opacity: 0.5;
	transition: all 0.25s;
	transform: translateX(-5px);
}

#navigation ul ul li:hover a:after {
	opacity: 1;
	transform: translateX(0);
}

/* Menu Hovers */
#navigation.style-1 ul li a {
	transition: 0.4s;
	border-radius: 50px;
	font-size: 15px;
}

#navigation.style-1 > ul > li > a.current,
#navigation.style-1 > ul > li:hover > a { 
	background: rgba(102, 103, 107, 0.06);
	color: #66676b;
}

#navigation.style-1 ul li li a { background: #fff !important }
#navigation.style-1 ul a {padding: 2px 15px;}
#navigation ul ul { margin: 10px 0 0 0; }
#navigation.style-1 ul li { margin: 0 3px; }
#navigation.style-1 { margin-top: 4px; }

body #navigation ul .mega-menu:not(.mm-panel) ul a,
body #navigation.style-1 ul ul li { opacity: 1; }

body #navigation.style-1 ul ul li a:after,
body #navigation.style-1 ul li:hover ul li a,
body #navigation.style-1 ul li:hover ul li:hover li a,
body #navigation.style-1 ul li:hover ul li:hover li:hover li a,
body #navigation.style-1 ul ul li:hover ul li a { color: #666; }

body #navigation.style-1 ul ul li:hover a:after,
body #navigation.style-1 ul li:hover ul li:hover a,
body #navigation.style-1 ul li:hover ul li:hover li:hover a,
body #navigation.style-1 ul li:hover ul li:hover li:hover li:hover a,
body #navigation.style-1 ul ul li:hover ul li a:hover { color: #66676b; }


/* ---------------------------------- */
/* Mega Menu
------------------------------------- */
.mega-menu {
	opacity: 0;
	visibility: hidden;
	transition: all 0.25s;
	position: absolute;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.12);
	border-radius: 4px;
	font-size: 15px;
	display: flex;
	top: 100%;
	left: 0;
	padding: 20px 0;
	box-sizing: border-box;
	font-size: 14px;
	margin-top: -2px;
	background-color: #fff;
	width: 200px;
	z-index: 99;
	transform: translate3d(-42%,24px,0);
}

.mega-menu:before {
	content: "";
	position: absolute;
	top: -12px;
	left: 0;
	display: block;
	width: 100%;
	height: 12px;
	opacity: 0;
}

#navigation ul li .mega-menu ul { 
	opacity: 0;
	visibility: hidden;
	transform: translate3d(0,0,0);
	position: relative;
	display: inline-block;
	flex: 1;
	padding: 0;
	margin: 0;
	box-shadow: none;
	border-radius: 0;
	top: auto;
	left: auto;
	background-color: transparent;
}

#navigation ul li:hover .mega-menu {
	opacity: 1;
	visibility: visible;
	transform: translate3d(-42%,12px,0);
}

#navigation ul li:hover .mega-menu ul {
	opacity: 1;
	visibility: visible;
}


#navigation .mega-menu ul,
#navigation .mega-menu ul li {
	width: 100%;
	opacity: 1 !important;
}

.mega-menu.two-columns    { width: 429px;}
.mega-menu.three-columns  { width: 643px;}
.mega-menu.four-columns   { width: 857px;}

#navigation ul .mega-menu ul a {
	color: #444;
	padding: 0 !important;
	display: block;
	margin-left: 0;
	-wekkit-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
	opacity: 0.8;
}

#navigation ul .mega-menu ul a:hover {
	opacity: 1;
}

#navigation ul li:hover .mega-menu ul a, 
#navigation ul .mega-menu ul a {
    padding: 5px 0 !important;
    line-height: 22px !important;
    transform: translate3d(0,0,0) !important;
}

#navigation .mega-menu ul li {
	padding: 0 30px;
}

#navigation ul li a i {
	font-size: 14px;
	position: relative;
	top: 0;
	padding-right: 5px;
}

.mega-menu-section {
	display: inline-block;
	float: left;
	width: 214px;
	padding: 0;
	border-right: 1px solid #e9e9e9;
}

.mega-menu .mega-menu-section:last-child { border: none; }

.mega-menu-headline {
	color: #222;
	opacity: 1 !important;
	font-weight: 600;
	display: block;
	padding: 8px 30px !important;
	letter-spacing: 0;
	font-size: 14px;
	line-height: 21px;
}

/* Some mega menu styles for mobile nav */
.mobile-styles .mm-listview { margin: 0px -20px !important; }
.mobile-styles .mm-listview .mega-menu-headline { color: #fff; padding: 15px 20px !important; background: #222 !important; }
.mobile-styles .mm-listview li a i { padding-right: 5px; }
.mobile-styles.mm-panel:before { height: 10px !important; }

/* Flickering Fix */
#navigation ul li .mega-menu ul,
#navigation ul li ul { z-index: 100; }
#navigation ul li:hover .mega-menu,
