	line-height: 22px;
	margin: 0;
	font-weight: 400;
	display: inline-block;
	margin-left: 6px;
	font-size: 14px;
	font-weight: 400;
	position: relative;
    color: #fff;
}

.main-search-inner .highlighted-category i {
    display: inline-block;
    float: left;
    font-size: 15px;
	position: relative;
}


.main-search-inner .highlighted-category:hover h4 { color: #fff; }

.highlighted-categories-headline {
	font-size: 16px;
	color: #777;
	font-weight: 500;
	margin-top: 50px;
	margin-bottom: 8px;
}

@media (max-width: 991px) {
	.main-search-container { height: auto; }
	.main-search-inner { position: relative; top: 0; transform: none; padding-top: 30px; padding-bottom: 45px; }
}


/* Tyepd Words */
body .typed-cursor {
    opacity: 0.5;
    font-weight: 200;
    margin-left: 2px;
}

body .typed-cursor.typed-cursor--blink{
	 animation: typedjsBlink 1.5s infinite;
}

 @keyframes typedjsBlink{
     0% { opacity: 1; }
     50% { opacity: 0; }
     100% { opacity: 1; }
}

body .typed-fade-out{
	  opacity: 0;
	  transition: opacity .25s;
  }

body .typed-cursor.typed-cursor--blink.typed-fade-out{
	animation: 0;
}

@media (max-width: 768px) {
    .typed-words { display: block; width: 100%; min-height: 46px; }  
    .typed-cursor { display: none; }
}


/* ---------------------------------- */
/* Main Search Container
------------------------------------- */
.main-search-container {
	display: block;
	width: 100%;
	position: relative;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50%;
	height: 620px;
	z-index: 9;
}

.main-search-container.centered { text-align: center; }
.main-search-input { text-align: left; }

.main-search-container:before {
	content: "";
	position: absolute;
	height: 100%;
	width: 100%;
	display: block;
	background: linear-gradient(to right, rgba(255,255,255,0.99) 20%, rgba(255,255,255,0.7) 70%, rgba(255,255,255,0) 95%);
}

.main-search-container h2 {
	font-size: 42px;
}

.main-search-container h4 {
	font-size: 24px;
	font-weight: 300;
	color: #777;
	line-height: 32px;
}

/* Video */
.video-container {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -10;
	overflow: hidden;
}
.main-search-container video {
	top: 50%;
	transform: translateY(-52%);
	left: 0;
	min-width: 100%;
	min-height: 100%;
	width: auto;
	height: auto;
	z-index: -10;
	background-size: cover;
	transition: 1s opacity;
	position: absolute;
}


/* Dark Overlay */
.main-search-container.dark-overlay:before { background: rgba(46,47,50,0.55); }
.main-search-container.dark-overlay h2 { color: #fff; }
.main-search-container.dark-overlay h4 { color: rgba(255,255,255,0.7); }


/* Inner Content */
.main-search-inner {
	position: relative;
	display: block;
	top: 50%;
    transform: translate(0, -50%);
	padding-bottom: 30px;
}

.main-search-inner select {
	border: none;
	padding-top: 2px;
	padding-bottom: 0;
	height: 44px;
	box-shadow: none;
}


/* Main Search Input */
.main-search-input {
	margin-top: 50px;
	border-radius: 50px;
	width: 100%;
	background-color: #fff;
	box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.12);
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	padding: 9px;
	max-height: 68px;
}

.main-search-input-item {
	flex: 1;
	border-right: 1px solid #e9e9e9;
	margin-top: 3px;
	position: relative;
	padding-left: 30px;
	padding-right: 30px;
}

.main-search-input  .main-search-input-item:nth-last-child(2) {
	border-right: none;
	padding-left: 15px;
	padding-right: 15px;
}

.main-search-input-item.location a {
	position: absolute;
	right: 20px;
	top: 50%;
	transform: translateY(-50%);
	color: #999;
	padding: 10px;
	background: #fff;
}

.main-search-input-item.location a:hover { color: #66676b; }

.main-search-input input,
.main-search-input input:focus {
	font-size: 16px;
	border: none;
	background: #fff;
	margin: 0;
	padding: 0;
	height: 44px;
	line-height: 44px;
    box-shadow: none;
}

.main-search-input button.button {
	font-size: 18px;
	font-weight: 600;
	padding: 0 40px;
	margin-right: 1px;
	height: 50px;
	outline: none;
}

/* Gray Style */
.gray-style.main-search-input {
	box-shadow: none;
	background-color: #f7f7f7;
	top: -10px;
	position: relative;
}

.gray-style .main-search-input-item.location a,
.gray-style.main-search-input input,
.gray-style.main-search-input input:focus {
	background: #f7f7f7;
}

.gray-style .main-search-input-item { border-color: #e0e0e0; }

/* Home Improvements */
  
.transparent-header .main-search-container  { height: auto; }

.transparent-header .main-search-container .main-search-inner {
    top: 0;
    transform: none;
    padding-top: 210px;
    padding-bottom: 170px;
}

.transparent-header .main-search-container { height: auto; }

.transparent-header .main-search-container:before {
    background: rgba(0,0,0,0.5);
}

.transparent-header #header:not(.cloned) {
    background: rgba(51,51,51,0);
    position: absolute;
    width: 100%;
    box-shadow: none;
    border-bottom: 1px solid rgba(255,255,255,0.15);
}

.transparent-header .main-search-container:before { background: rgba(51,51,51,0.85); }
.transparent-header .highlighted-categories-headline,
.transparent-header .main-search-container h2 { color: #fff; }

.transparent-header #header:not(.cloned) #navigation.style-1 > ul > li > a { color: #fff; }
.transparent-header #header:not(.cloned) #navigation.style-1 > ul > li > a.current, 
.transparent-header #header:not(.cloned) #navigation.style-1 > ul > li:hover > a {
    background: #66676b;
    color: #fff;
}
.transparent-header .main-search-container h4 { color: rgba(255,255,255,0.65); }
.transparent-header .highlighted-category h4 { color: #fff; }
.transparent-header .header-widget .sign-in:hover,
.transparent-header .header-widget .sign-in { color: #fff; }

.transparent-header #header:not(.cloned) .user-menu .user-name:after {color: rgba(255,255,255,0.7) !important; }
.transparent-header #header:not(.cloned) .user-menu.active .user-name,
.transparent-header #header:not(.cloned) .user-menu:hover .user-name,
.transparent-header #header:not(.cloned) .user-menu .user-name { color: #fff !important; }

.transparent-header #header:not(.cloned) .header-widget .button,
.transparent-header #header:not(.cloned) .header-widget .button.border {
    background: rgba(255,255,255,0.15);
    color: #fff;
    border: none;
    padding: 9px 20px;
}

.transparent-header #header:not(.cloned) .header-widget .button:hover,
.transparent-header #header:not(.cloned) .header-widget .button.border:hover {
	background: #66676b;
	color: #fff;
}

.transparent-header .main-search-container h2 {
    font-size: 44px;
    font-weight: 500;
    line-height: 50px;
}

.transparent-header .main-search-inner .highlighted-category:hover {
    color: #333;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    background-color: #fff;
}

.transparent-header .main-search-inner .highlighted-category:hover h4 { color: #333; }

.main-search-inner .highlighted-category { float: none; }
.main-search-inner .highlighted-category i {float: none; }
.main-search-inner .highlighted-category {
    padding: 3px 15px;
    display: inline-block;
}

.main-search-form { text-align: left; }
.main-search-inner .highlighted-category h4 {
    top: -1px;
    position: relative;
}

.transparent-header .main-search-inner .highlighted-category {
    background-color: rgba(255, 255, 255, 0.15);
    box-shadow: none;
}


@media (min-width: 1240px) and (max-width: 1440px) {
    .transparent-header .main-search-container .main-search-inner {
        padding-top: 190px;
        padding-bottom: 150px;
    }
}

/* Transparent Header Mobile Styles  */
@media (max-width: 1240px) {
    .transparent-header #navigation.style-1:before {
        background: rgba(255,255,255,0.15);
    }
    .transparent-header #header:not(.cloned) { border: none; }
