body .alt-search-box .main-search-input-item .chosen-container-active.chosen-with-drop .chosen-single {
		padding: 0 20px;
		height: 55px;
		line-height: 55px;
		border-radius: 4px;
		background: #fff;
		border: 1px solid #e2e2e2;
}

#booking-date-search { cursor: pointer; }

.alt-search-box button.button { margin-top: 15px; }

