	font-style:  normal;
	font-weight: 600;
	color:  #fff;
	position: relative;
}

.home-search-slide h3 a:before,
.home-search-slide h3 strong:before {
	content: "";
	height:  50%;
	background: #66676b;
	display: block;
	position: absolute;
	bottom:  0;
	left:  0;
	height:  20%;
	width:  100%;
	z-index: -1;
	transition: 0.3s;
}

.home-search-slide h3 a:hover:before,
.home-search-slide h3 strong:hover:before {
	height:  100%;
}

.slider-controls-container {
	position: absolute;
	left: 0;
	right:  0;
	margin:  0 auto;
	display:  block;
	text-align: center;
	bottom: -29px;
}

