.verified-badge:hover { border-radius: 4px 4px 0 0; }

.verified-badge:hover .tip-content {
	opacity: 1;
	visibility: visible;
	transform: translate(-50%, 100%);
	-ms-transform: translate(-50%, 100%);
}

.verified-badge .tip-content:after {
	font-family: "FontAwesome";
	content:"\f0d8";
	position: absolute;
	top: -3px;
	bottom: auto;
	left: 50%;
