/* Time Slot Labels */
.time-slot {
	display: block;
}

.time-slot label {
  border-radius: 3px;
  border: none;
  background-color: #f6f6f6;
  width: 100%;
}

/* hide input */
.time-slot input:empty {
	display: none;
}

/* style label */
.time-slot label i {
	position: relative;
	font-size: 18px;
	top: 2px;
	margin-right: 2px;
}

.time-slot input:empty ~ label {
	position: relative;
	float: left;
	padding: 12px 10px;
	text-align: center;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	border-radius: 4px;
	color: #777;
	transition: 0.3s;
	overflow: hidden;
	font-size: 15px;
}

.time-slot:last-child label {
	margin-bottom: 0;
}

.time-slot input ~ label:hover {
	color: #66676b;
	background-color: rgba(102, 103, 107, 0.08);
}

.time-slot input:checked ~ label:hover {
	color: #fff;
}
.time-slot input:checked ~ label {
	color: #fff;
	background-color: #66676b;
}

.time-slot input:checked ~ label span {
	color: #fff;
	opacity: 0.8;
}

.time-slotinput:empty ~ label:after,
.time-slot input:empty ~ label:before {
	position: absolute;
	display: block;
	top: 0;
	bottom: 0;
	left: 0;
	content: '';
	width: 100%;
	height: 100%;
	text-align: center;
	border-radius: 4px;
	background: transparent;
	z-index: 100;
	opacity: 0;
}

.time-slot label span,
.time-slot label strong {
	font-weight: 600;
	display: block;
	padding: 0;
	margin: 0;
	line-height: 19px;

}

.time-slot label span{
	font-weight: 500;
	opacity: 0.5;
	transition: 0.3s;
}

.time-slot label:hover span {
	color: #66676b;
	opacity: 0.6;
}




