}

.review-images a img {border-radius: 4px; }


/* Add Comment */
.add-review-box {
	border-radius: 4px;
	background-color: #f9f9f9;
	padding: 36px;
	padding-bottom: 40px;
	margin-bottom: 55px;
	margin-top: 70px;
