/* ---------------------------------- */
/* Blog Styles
------------------------------------- */
.blog-post {
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
	background-color: #fff;
	display: inline-block;
	width: 100%;
	border-radius: 3px;
	margin: 15px 0;
}

section.fullwidth .blog-post { margin: 5px 0 0 0; }

.post-img {
	position: relative;
	height: 100%;
	width: 100%;
	display: block;
}

.post-img img {
	width: 100%;
	border-radius: 3px 3px 0 0;
}

.post-img:after {
	position: absolute;
	height: 100%;
	width: 100%;
	display: block;
	top: 0;
	left: 0;
	content: "";
	z-index: 9;
	opacity: 0.1;
	background: #333;
	transition: opacity 0.4s;
	border-radius: 3px 3px 0 0;
}

.post-img:hover:after {
	opacity: 0.5;
}


/* Post Hover Icon */
.post-img:before {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	display: block;
	top: 50%;
	right: 0;
	left: 0;
	margin: 0 auto;
	text-align: center;
	content: "";
	z-index: 119;
	transition: 0.3s;
	transform: translateY(-80%);
	width: 50px;
	height: 50px;
	text-align: center;
	border-radius: 50%;
	background-color: #66676b;
	background-image: url(../images/plus-icon.svg);
	background-repeat: no-repeat;
	background-position: 50%
}

.post-img:hover:before {
	opacity: 1;
	visibility: visible;
	transform: translateY(-50%);
}


/* Content Styling */
.post-content { padding: 38px; }

.post-content p { margin: 18px 0 0 ; }

.post-content h3 {
	margin: 0;
	line-height: 34px;
	font-size: 22px;
}

.post-content h3 a { color: #333; }
.post-content h3 a:hover {color: #66676b; }

.post-content .meta-tags span {
	margin: 0 0 15px 0;
	display: inline-block;
	color: #888;
}

.post-content a.read-more {
	color: #66676b;
	margin: 23px 0 2px 0;
	display: inline-block;
}

a.read-more i {
	font-size: 20px;
	position: relative;
	padding-left: 4px;
	top: 1px;
	transition: 0.2s;
}

 a.read-more:hover i {
	padding-left: 7px;
}


