	list-style: none;
	padding: 0;
	margin: 5px 0 0 0;
}

.post-meta li {
	display: inline-block;
	color: #888;
}

.post-meta li:after {
	content: "";
	width: 1px;
	height: 12px;
	background: #d0d0d0;
	display: inline-block;
	position: relative;
	top: 0px;
	margin: 0 10px;

}

.post-meta li:last-child:after { display: none; }

.post-meta li a { color: #888; }

.post-meta li a:hover {
	color: #66676b;
