/* ---------------------------------- */
/* Post Navigation
------------------------------------- */
#posts-nav {
	padding: 0;
	list-style: none;
	width: 100%;
	position: relative;
	margin: 0;
	display: inline-block;
	min-height: 60px;
}

#posts-nav li span {
	display: block;
	color: #888;
	font-size: 15px;
	margin-bottom: 1px;
	font-weight: 400;
}

#posts-nav li {
	position: absolute;
	font-size: 17px;
	font-weight: 500;
	width: 50%;
}

#posts-nav li a {
	transition: transform 0.3s, color 0.3s 0s;
	color: #333;
	display: inline-block;
}

#posts-nav li a:hover { color: #66676b; }

#posts-nav li.next-post {
	right: 0;
	text-align: right;
}

#posts-nav li.prev-post {
	left: 0;
}

#posts-nav li.next-post a { padding-right: 50px; }
#posts-nav li.prev-post a { padding-left: 50px; }

#posts-nav li.prev-post a:before,
#posts-nav li.next-post a:before {
	font-family: "simple-line-icons";
	font-size: 20px;
	position: absolute;
	color: inherit;
	top: 50%;
	transform: translate3d(0,-50%,0);
	transition: transform 0.3s;
}

#posts-nav li.next-post a:before {
	right: 0;
	content: "\e606";
}

#posts-nav li.prev-post a:before {
	left: 0;
	content: "\e605"
}

#posts-nav li.next-post a:hover:before { transform: translate3d(5px,-50%,0); }
#posts-nav li.prev-post a:hover:before { transform: translate3d(-5px,-50%,0); }


/* ---------------------------------- */
/* Pricing Tables
------------------------------------- */
.pricing-container {
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	position: relative;
}

.plan {
	flex: 1;
	box-shadow: 0px 0px 12px 0px rgba(0,0,0,0.1);
	text-align: center;
	position: relative;
	z-index: 9;
	overflow: hidden;
}

.plan-price {
	width: 100%;
	background: #f9f9f9;
	padding: 45px 30px;
	padding-top: 48px;
}

.plan:first-child .plan-price { border-radius: 4px 0 0 0; }
.plan:first-child { border-radius: 4px 0 4px 4px; }

.plan:last-child .plan-price { border-radius: 0 4px 0 0; }
.plan:last-child { border-radius: 0 4px 4px 0; }

.plan-price h3 {
	margin: 0;
	color: #888;
	font-weight: 400;
	font-size: 22px;
}

.plan.featured .listing-badge {
	background-color: #fff;
	color: #66676b;
}

.plan-price .value {
	display: block;
	font-size: 37px;
	line-height: 35px;
	padding: 22px 0 0 0;
	color: #333;
}

.plan-price .period {
	color: #888;
	margin-top: 20px;
	padding: 0 10%;
	display: block;
}

.plan-features {
	padding: 35px 30px;
	background: #fff;
	border-radius: 4px;
}

.plan-features ul {
	list-style: none;
	padding: 0;
}

.plan-features ul li {
	padding: 3px 0;
}

.plan-features a.button {
	margin-top: 20px;
}


/* featured plan*/
.plan.featured {
	margin-left: -1px;
	margin-right: -1px;
	transform: translateY(-23px);
	border-radius: 0 0 4px 4px;
	z-index: 11;
}

.plan.featured .plan-price {
	background-color: #66676b;
	padding: 50px 30px;
	border-radius: 4px 4px 0 0;
}

.plan.featured .plan-price h3 {
	color: #fff;
}

.plan.featured .plan-price .value {
	color: #fff;
}

.plan.featured .plan-price .period {
	color: #fff;
	line-height: 23px;
}

.plan.featured .plan-features { padding: 45px 30px; }


.plan.featured .listing-badges .featured {
	background: #fff;
	color: #66676b;
}


/* ---------------------------------- */
/* List Styles
------------------------------------- */

.list-4,
.list-3,
.list-2,
.list-1 {
	padding: 3px 0 0 0;
	font-size: 16px;
}

.list-4 li,
.list-3 li,
.list-2 li,
.list-1 li {
	list-style: none;
	margin: 14px 0;
	line-height: 27px;
	margin-left: 22px;
	position: relative;
}

.list-1.alt li { margin: 12px 0; margin-left: 22px; }

.list-4 li:first-child,
.list-3 li:first-child,
.list-2 li:first-child,
.list-1 li:first-child {
	margin-top: 0;
}


.list-4 li:before,
.list-3 li:before,
.list-2 li:before,
.list-1 li:before {
	font-family: "FontAwesome";
	margin: 0;
	position: relative;
	color: #66676b;
	float: left;
	margin-left: -22px;
	display: block;
}

.list-1.gray li:before {
	color: #777;
}

.list-4 li:before { content: "\f192"; font-size: 14px; top: 1px; }
.list-3 li:before { content: "\f058"; font-size: 15px; top: 1px; }
.list-2 li:before { content: "\f00c"; font-size: 14px; }
.list-1 li:before { content: "\f0c8"; font-size: 8px; }

.list-4 li { margin-left: 27px; }
.list-4 li:before { margin-left: -27px; }


/* Numbered Style */
.numbered ol {
	counter-reset: li;
	list-style: none;
	padding: 0;
	margin-left: 18px;
	display: inline-block;
	font-size: 16px;
}

.numbered ol li {
	display: inline-block;
	padding: 6px 0;
	width: 100%;
}

.numbered ol > li::before {
	content: counter(li);
	counter-increment: li;
	font-size: 16px;
	line-height: 33px;
	width: 36px;
	height: 36px;
	display: inline-block;
	border: 1px solid #333;
	border-radius: 50%;
	text-align: center;
	margin: 0;
	margin-left: 0;
	font-weight: 500;
	color: #333;
	position: relative;
	float: left;
	left: -18px;
	font-size: 15px;
	transform: translateY(-10%);
}

.numbered.color ol > li::before {
	border: 1px solid #66676b;
	color: #66676b;
}

.numbered.color.filled ol > li::before {
	border: 1px solid #66676b;
	color: #fff;
	background-color: #66676b;
}

mark.color {
    background-color: #66676b;
    border-radius: 4px;
}

mark { border-radius: 4px; }

.demo h1, .demo h2, .demo h3, .demo h4, .demo h5, .demo h6 {
    line-height: 40px;
}

