/*----------------------------------- */
/* Image Hovers
------------------------------------- */
.img-box {
	height: 350px;
	display: inline-block;
	position: relative;
	width: 100%;
	overflow: hidden;
	z-index: 90;
	margin: 10px 0;
	border-radius: 3px;
}

.img-box-background {
	background-size: cover;
	background-position: 50%;
	width: 100%;
	height: 100%;
	transition: all 0.55s;
	position: absolute;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    transition: transform 0.35s ease-out;
}

.img-box:hover .img-box-background { transform: scale(1.07); }

.img-box:before {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: block;
	content: "";
	background-color: #222;
	opacity: 0.3;
	z-index: 99;
	transition: 0.3s ease-out;
}

.img-box-content {
	position: absolute;
	z-index: 101;
	top: 50%;
	margin: 0 auto;
	text-align: center;
	width: 100%;
	transform: translate(0,-50.5%);
}

.img-box-content h4 {
	font-size: 28px;
	line-height: 36px;
	color: #fff;
	margin: 0;
}

.img-box-content span {
    color: rgba(255,255,255,0.9);
    background: rgba(255,255,255,0.12);
    padding: 4px 12px;
    margin-top: 4px;
    line-height: 20px;
    display: inline-block;
    border-radius: 50px;
    font-size: 15px;
    transition: 0.3s;
}


.alternative-imagebox .img-box-content {
    transform: none;
    top: initial;
    bottom: 30px;
    padding-left: 30px;
    text-align: left;
}

.alternative-imagebox .img-box-content h4 { font-size: 22px; }
.alternative-imagebox.img-box { height: 270px; }

.alternative-imagebox.img-box:before {
	opacity: 1;
	background: -webkit-linear-gradient(bottom, rgba(35,35,37,0.7) 0%, rgba(35,35,37,0.35) 45%, rgba(22,22,23,0.1) 60%, rgba(0,0,0,0) 100%);
	background: -o-linear-gradient(bottom, rgba(35,35,37,0.7) 0%, rgba(35,35,37,0.35) 45%, rgba(22,22,23,0.1) 60%, rgba(0,0,0,0) 100%);
	background: linear-gradient(to top, rgba(35,35,37,0.7) 0%, rgba(35,35,37,0.35) 45%, rgba(22,22,23,0.1) 60%, rgba(0,0,0,0) 100%);
	background-color: rgba(35,35,37,0);
}




