	background-position: 50% 50%;
	background-repeat: no-repeat;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

.edge-bg:before {
	position: absolute;
	content: "";
	width: 100%;
	height: 100%;
