/* ------------------------------------------------------------------- */
/*  04. Dashboard
---------------------------------------------------------------------- */
#dashboard {
	background-color: #f7f7f7;
	min-height: 100vh;
	display: flex;
	flex-wrap: wrap;
	padding-top: 80px;
}

/* Flexbox Adjustments */
.dashboard-content,
.dashboard-nav { flex: 1; }

.dashboard-content {
	padding: 40px 45px;
	padding-bottom: 0;
	position: relative;
	z-index: 10;
	height: 100%;
	margin-left: 260px;
}

.dashboard-nav,
.dashboard #logo {
	min-width: 260px;
	max-width: 260px;
}

.dashboard-nav {
	position: fixed;
	min-height: 100vh;
	overflow: hidden;
}


/* Navigation Scrolling */
.dashboard-nav-inner {
	height: 100%;
	width: 277px;
	overflow-y: scroll;
	position: relative;
	display: block;
	padding: 10px 0 25px 0;
}

.messages-container-inner .messages-inbox ul::-webkit-scrollbar { width: 17px; }

.dashboard-nav-inner {
    max-height: 900px;
    width: calc(100% + 17px);
    overflow-y: scroll;
    position: relative;
    display: block;
    padding: 10px 0 25px 0;
}

@media (max-width: 991px) {
	.dashboard-nav-inner {
	    max-height: 900px;
	    width: calc(100% + 18px);
	}
}
/* Navigation Scrolling - End */


header.fullwidth.dashboard .container { padding-left: 285px; }

/* Dashboard Logo */
.dashboard #logo {
	display: block;
	background-color: #333;
	position: absolute;
	height: 100%;
	left: 0;
	top: 0;
	margin: 0;
}

.dashboard #logo a {
	margin-left: 33px;
	display: inline-block;
	height: 100%;
}

.dashboard #logo a img {
	top: 50%;
	position: relative;
	transform: translate3d(0,-50%,0);
}


/* Dashboard Footer */
#dashboard .copyrights {
	padding: 40px 0;
	margin: 0;
	display: block;
	text-align: left;
	border: none;
}

.dashboard #logo a { display: none; }
.dashboard #logo a.dashboard-logo { display: inline-block;}

/* ---------------------------------- */
/* Dashboard Navigation
------------------------------------- */
.dashboard-nav {
	background-color: #2a2a2a;
	display: block;
	box-shadow: 0px 0px 12px 0px rgba(0,0,0,0.12);
	z-index: 11;
}

a.dashboard-responsive-nav-trigger { display: none; }

.dashboard-nav ul {
	list-style: none;
	padding: 0;
	margin: 15px 0 0 0;
}

.dashboard-nav ul:before {
	content: attr(data-submenu-title);
	padding: 0 30px;
	margin-bottom: 5px;
	display: block;
	color: #999;
	text-transform: uppercase;
	font-weight: 300;
	font-size: 12px;
	letter-spacing: 1px;
}

/* 1st Level */
.dashboard-nav ul li {
	color: #eee;
	display: block;
	border-left: 3px solid transparent;
	transition: 0.3s;
	line-height: 24px;
	font-size: 15px;
}

.dashboard-nav ul li a {
	color: #eee;
	display: block;
	padding: 11px 27px;
	transition: 0.3s;
	cursor: pointer;
}

.dashboard-nav ul li a i {
	padding-right: 8px;
	width: 20px;
}

.dashboard-nav ul li a:hover { color: #fff; }


/* Nav Tag */
.dashboard-nav ul li span.nav-tag {
	background-color: rgba(255,255,255,0.2);
	font-family: "Open Sans";
	font-weight: 600;
	display: inline-block;
	font-size: 11px;
	line-height: 20px;
	color: #fff;
	padding: 0;
	padding: 0 7px;
	box-sizing: border-box;
	text-align: center;
	min-width: 20px;
	height: 20px;
	letter-spacing: -0.5px;
	text-align: center;
	border-radius: 50px;
	margin-left: 4px;
}

.dashboard-nav ul li span.nav-tag.messages {top: -2px; position: relative; }
.dashboard-nav ul li span.nav-tag.messages,
.dashboard-nav ul li span.nav-tag.green { background-color: #64bc36; }
.dashboard-nav ul li span.nav-tag.yellow { background-color: #b9be1c; }
.dashboard-nav ul li span.nav-tag.red { background-color: #ee3535; }


/* Dropdown Styles */
.dashboard-nav ul li ul {
	padding: 0;
	margin: 0;
	visibility: hidden;
	max-height: 0;
	opacity: 0;
	line-height: 0;
	transition: 0.2s;
}

.dashboard-nav ul li.active ul {
	visibility: visible;
	padding: 0 0 15px 0;
	line-height: 24px;
	opacity: 1;
	max-height: 300px;
}

.dashboard-nav ul li ul li {
	color: #eee;
	background-color: transparent;
	border: none;
	padding-left: 54px;
	position: relative;
	margin: 0;
}

.dashboard-nav ul li ul li a { padding: 7px 0; }

.dashboard-nav ul li ul li:hover {
	color: #fff;
	border: none;
	background-color: transparent;
}

/* Indicator Arrow */
.dashboard-nav ul li a:after {
	font-family: "FontAwesome";
	content: '\f107';
	right: 27px;
	font-size: 16px;
	position: absolute;
	transition: 0.2s;
	color: rgba(255,255,255,0.5);
}


.dashboard-nav ul li.active a:after { transform: rotate(180deg); }

.dashboard-nav ul li ul:before,
.dashboard-nav ul li a:only-child:after { content: ''; display: none; }

/* Hover State */
.dashboard-nav ul li ul li:hover a { padding-left: 4px; }

.dashboard-nav ul li.active,
.dashboard-nav ul li:hover {
	color: #fff;
	border-color: #66676b;
	background-color: rgba(255,255,255,0.05);
}


