/* Add Listing
------------------------------------- */
.add-listing-section {
	border-radius: 4px;
	background-color: #fff;
	box-shadow: 0 0 12px 0 rgba(0,0,0,0.06);
	padding: 0 40px 25px 40px;
}

.add-listing-headline {
	width: calc(100% + 80px);
	left: -40px;
	position: relative;
	padding: 30px 40px;
	margin: 0 0 30px 0;
	border-radius: 4px 4px 0 0;
	background-color: #fcfcfc;
	border-bottom: 1px solid #eaeaea;
}

.add-listing-headline h3 {
	padding: 0;
	margin: 0;
	line-height: 32px;
	font-size: 22px;
	position: relative;
	top: 1px;
}

#add-listing h5 {
	font-size: 15px;
	font-weight: 600;
	color: #333;
	margin-bottom: 17px;
}

#add-listing h5 span {
	color: #888;
	font-weight: 400;
}

#add-listing h5 i {
	padding-right: 4px;
}

.add-listing-headline i {
	padding-right: 7px;
	display: inline-block;
	position: relative;
	top: 1px;
}

.button.preview {
	margin-top: 40px;
	font-size: 18px;
	font-weight: 400;
	padding: 11px 26px;
	display: inline-block;
}

.button.preview i { padding: 0; margin: 0 0 0 4px;}

.add-listing-section .row.with-forms input,
.add-listing-section .row.with-forms input[type="text"],
.add-listing-section .row.with-forms input[type="password"],
.add-listing-section .row.with-forms input[type="email"],
.add-listing-section .row.with-forms input[type="number"],
.add-listing-section .row.with-forms textarea,
.add-listing-section .row.with-forms input,
.add-listing-section .row.with-forms select,
.add-listing-section .row.with-forms .chosen-container {
    margin-bottom: 22px;
}
.add-listing-section textarea { min-height: 200px; margin-bottom: 35px; }

#add-listing h5.fb-input { color: #3b5998; }
#add-listing h5.twitter-input { color: #1da1f2; }
#add-listing h5.gplus-input { color: #dd4b39; }


/* Tips */
.tip {
	font-style: normal;
	position: relative;
	top: -1px;
	left: 2px;
	display: inline-block;
	cursor: help;
	color: #fff;
	font-size: 11px;
	line-height: 0;
	width: 18px;
	height: 18px;
	background: #66676b;
	display: inline-block;
	border-radius: 50%;
	color: #fff;
	text-align: center;
	padding: 0 !important;
	font-weight: 500;
}

.tip-content {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translate(-50%, calc(-100% - 20px));
	-ms-transform: translate(-50%, -100%);
	background: #333;
	color: #fff;
	padding: 13px 15px;
	min-width: 200px;
	max-width: 200px;
	font-size: 13px;
	border-radius: 4px;
	line-height: 19px;
    pointer-events: none;
    z-index: 999;
	text-align: center;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s;
}

.tip:hover .tip-content {
	opacity: 1;
	visibility: visible;
	transform: translate(-50%, calc(-100% - 13px));
	-ms-transform: translate(-50%, -115%);
}

.tip:after {
	font-family: "FontAwesome";
	content: "\f128";
	line-height: 19px;
	display: inline-block;
	position: relative;
	text-align: center;
	-moz-transform: translateY(-1px);
}

.tip-content:after {
	font-family: "FontAwesome";
	content:"\f0dd";
	position: absolute;
	bottom: 3px;
	left: 50%;
	font-size: 22px;
	color: #333;
	transform: translate(-50%,50%);
}

/* ---------------------------------- */
/* Opening Hours
------------------------------------- */
.opening-day { margin-bottom: 22px; }
.opening-day h5 { margin-top: 17px; transition: 0.15s; }
.opening-day:hover h5 { color: #66676b !important; }
.opening-day .chosen-container .chosen-results { max-height: 200px; }


/* ---------------------------------- */
/* Slots
------------------------------------- */
.add-listing-headline i.fa { top: -2px; }

.availability-slots {
	display: flex;
	flex-wrap: wrap;
	width: calc(100% + 30px);
}

.day-slots {
	margin: 0 25px 30px 0;
	flex: 0 0 calc(25% - 25px);
}

/* 24 hour clock type */
.availability-slots.twenty-four-clock i.am-pm,
.availability-slots.twenty-four-clock select.twelve-hr { display: none; }

/* Slot Headline */
.day-slot-headline {
	background-color: #66676b;
	color: #fff;
	text-align: center;
	border-radius: 4px;
	padding: 7px 10px;
	position: relative;
	cursor: default;
	font-weight: 600;
}

/* No Slots */
.no-slots {
	background-color: #f0f0f0;
	border-radius: 4px;
	margin-top: 8px;
	padding: 10px;
	display: flex;
	cursor: default;
	text-align: center;
	width: 100%;
	display: block;
	font-size: 14px;
	font-weight: 600;
	transition: 0.4s;
	opacity: 1;
	max-height: 60px;
	padding-top: 10px;
	padding-bottom: 10px;
	margin-top: 8px;
}


.no-slots,
.no-slots-fadeout {
	opacity: 0;
	max-height: 0;
	padding-top: 0;
	padding-bottom: 0;
	margin-top: 0;
}


.no-slots.no-slots-fadein {
	opacity: 1;
	max-height: 60px;
	padding-top: 10px;
	padding-bottom: 10px;
	margin-top: 8px;
	display: block;
}


/* Single Slot */
.single-slot {
	background-color: #f3f3f3;
	border-radius: 4px;
	margin-top: 8px;
	padding: 0;
	display: flex;
	cursor: move;
}

.single-slot.slot-animation {
	animation-name: slotFadeIn;
	animation-duration: 0.3s;
}

@keyframes slotFadeIn {
	0% { opacity: 0; max-height: 0; }
	100% { opacity: 1; max-height: 150px; }
}

.single-slot.cloned { display: none; }

.single-slot-left {
	flex: 1;
	padding: 12px 17px;
}

.single-slot-right { 
    flex: 0 0 auto;
    background-color: #eee;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 4px 4px 0;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 17px;
}

.single-slot-right strong {
	display: flex;
	font-weight: 600;
	font-size: 14px;
	width: 100%;
	line-height: 20px;
	margin-bottom: 3px;
}

.single-slot-time {
	font-weight: 700;
	color: #666;
	line-height: 20px;
	font-size: 14px;
	margin-bottom: 4px;
}

.single-slot-time i.am-pm {
	font-style: normal;
}


/* Remove Button */
.remove-slot {
	background-color: #c8c8c8;
	color: #fff;
	border: none;
	border-radius: 3px;
	height: 19px;
	width: 19px;
	line-height: 17px;
	font-size: 11px;
	transition: 0.3s;
	font-weight: 500;
}

.remove-slot i {
	position: relative;
	left: -1px;
}

.remove-slot:hover {
	background-color: #ec2c2c;
}

/* Add Slot */
@keyframes shake {
	0%, 100% {transform: translateX(0);}
	10%, 30%, 50%, 70% {transform: translateX(-5px);}
	20%, 40%, 60% {transform: translateX(5px);}
}

.add-slot-shake-error {
	animation-name: shake;
	animation-duration: 0.6s;
}

.add-slot {
	display: flex;
	width: 100%;
	background-color: #333;
	border-radius: 4px;
	margin-top: 8px;
	padding: 8px;
}

.add-slot span {
	font-size: 14px;
	color: #aaa;
	padding: 0 8px;
	font-weight: 600;
	font-size: 12px;
	position: relative;
	top: 1px;
}

.add-slot-inputs {
	flex: 1;
	display: flex;
	padding: 0;
}

.add-slot-btn { flex: 0 0 1; }

.add-slot-btn button {
	outline: none;
	background-color: #fff;
	color: #333;
	border: none;
	border-radius: 4px;
	margin-left: 10px;
	font-size: 14px;
	font-weight: 600;
	height: 100%;
	padding: 0 15px;
	transition: 0.3s;
}

.add-slot-btn button:hover {
	background-color: #66676b;
	color: #fff;
}

.add-slot-inputs select,
.add-slot-inputs input {
	background-color: #4a4a4a;
	border: none;
	padding: 0;
	height: 28px;
	line-height: 1;
	color: #fff;
	font-size: 14px;
	font-weight: 600;
	margin: 0;
	margin-right:0;
	text-align: center;
	cursor: pointer;
	border-radius: 4px;
	width: 100%;
	box-shadow: none;
}

input[type="time"]::-webkit-clear-button{
    display:none;
}

.add-slot-inputs select {
	margin-right: 0;
	border-radius: 0 3px 3px 0;
	padding-left: 8px;
	border-left: 1px solid rgba(255,255,255,0.1);
	width: auto;
	font-size: 13px;
	margin-left: -4px;
}
.add-slot-inputs select option {
	background-color: #333;
	outline: none;
	border: none;
	cursor: pointer;
}

.add-slot-inputs input::-webkit-outer-spin-button,
.add-slot-inputs input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; 
}

.add-slot-inputs input{ padding-right: 4px; }
.availability-slots.twenty-four-clock .add-slot-inputs input{
    padding-right: 0;
}

/* Plus Minus Buttons */
.plusminus {
  display: inline-block;
  position: relative;
  font-size: 0;
  overflow: hidden;
  border-radius: 0;
  width: 44px;
  height: 72px;
}

.plusminus button {
  display: block;
  width: 24px;
  height: 24px;
  position: absolute;
  margin: 0;
  padding: 0;
  border: 0;
  background: #fff;
  color: #aaa;
  font-size: 10px;
  font-weight: 500;
  line-height: 1;
  font-family: FontAwesome;
  cursor: pointer;
  transition: 0.3s;
}

.plusminus button:focus {
	outline: none;
}

.plusminus button:hover {
	background-color: #333;
	color: #fff;
}

.plusminus button:disabled:hover {
  background: #bbb;
  color: #fff;
  cursor: default;
}

.plusminus button:first-child {
  bottom: 0;
}
.plusminus button:first-child:before {
  content: "\f068";
  line-height: 25px;
}
.plusminus button:last-child {
  top: 0;
}
.plusminus button:last-child:before {
  content: "\f067";
  line-height: 25px;
}

.plusminus input[type="number"] {
	position: absolute;
	width: 24px;
	height: 24px;
	top: 24px;
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 13px;
	line-height: 24px;
	font-weight: 700;
	text-align: center;
}
.plusminus input[type="number"]::-webkit-outer-spin-button, 
.plusminus input[type="number"]::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

.plusminus.horiz {
	width: 74px;
	height: 24px;
}

.plusminus.horiz button:first-child {
	left: 0;
	border-radius: 3px;
}

.plusminus.horiz button:last-child {
	right: 0;
	border-radius: 3px;
	margin-left: 1px;
}

.plusminus.horiz:after {
	bottom: 0;
}

.plusminus.horiz input[type="number"] {
	position: absolute;
	top: 0;
	left: 25px;
	border-radius: 3px;
}


/* Slots Media Queries */
@media (max-width: 1920px) {
	.day-slots {
		flex: 0 0 calc(25% - 25px);
	}
}

@media (max-width: 1660px) {
	.day-slots {
		flex: 0 0 calc(33.3% - 25px);
	}
}

@media (max-width: 1366px) {
	.day-slots {
		flex: 0 0 calc(33.3% - 25px);
	}
}

@media (max-width: 1365px) {
	.day-slots {
		flex: 0 0 calc(50% - 25px);
	}
}

@media (max-width: 1040px) {
	.availability-slots {
		width: 100%;
	}
	.day-slots {
		flex: 0 0 100%;
		margin-right: 0;
		margin-bottom: 30px;
	}
}

@media (max-width: 768px) {
	.availability-slots {
		width: 100%;
	}
	.day-slots {
		flex: 0 0 100%;
		margin-right: 0;
		margin-bottom: 30px;
		flex-direction: column;
	}
	.add-slot-inputs input { width: 100%; }
	.add-slot-inputs span { opacity: 0; width: 5px; height: 10px; }
	.add-slot-inputs { width: 100%; }
	.add-slot { flex-direction: column; }
	.add-slot-btn button {
		width: 100%;
		margin-left: 0;
		margin-top: 8px; 
		background-color: rgba(255,255,255,0.15); 
		color: #fff;
		padding: 3px 0;
	}
}


