	padding: 5px 0;
	margin: 0;
}

.category-box-content span {
	font-size: 18px;
	font-weight: 300;
	display: inline-block;
	color: rgba(255,255,255,0.7);
}

.category-box-btn {
	position: absolute;
	right: 32px;
	bottom: 32px;
	z-index: 111;
	background-color: transparent;
	border: 1px solid #fff;
	color: #fff;
	padding: 7px 0px;
	text-align: center;
	min-width: 120px;
	border-radius: 50px;
	transition: all 0.3s;
}

body .category-box-btn:hover {
	background-color: #fff;
	border-color: #fff;
	color: #333;
}

.category-box-content span {
    font-size: 18px;
    font-weight: 300;
    display: inline-block;
